.common-container {
  min-width: fit-content;
  flex: 1;
  box-sizing: border-box;
  border: 1px solid #ccc;
  margin-right: 8px;
  margin-bottom: 8px;
  overflow: auto;
  padding: 8px;
}
.counter-container {
  min-width: fit-content;
}
.counter-container .counter-item {
  margin-right: 24px;
}
.counter-container .counter-text {
  display: inline-block;
  width: 24px;
  text-align: center;
}
.counter-container button {
  cursor: pointer;
}
.todo-wrapper {
  min-width: fit-content;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.todo-wrapper .todo-title {
  font-weight: bold;
  text-align: center;
  line-height: 16px;
}
.todo-wrapper .todo-title i {
  font-size: 16px;
  display: block;
  margin-bottom: 10px;
  opacity: 0.6;
  text-shadow: 2px 2px 2px #999;
}
.todo-wrapper .todo-create input {
  display: inline-block;
  max-width: fit-content;
  padding: 4px;
  font-size: 16px;
}
.todo-wrapper .todo-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.todo-wrapper .todo-list .content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 10px 0;
  padding-bottom: 2px;
  cursor: pointer;
  border-bottom: 1px solid #ccc;
}
.todo-wrapper .todo-list .content .form {
  display: flex;
  align-items: center;
}
.todo-wrapper .todo-list .content .form span {
  font-size: 18px;
  display: inline-block;
  max-width: 150px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-top: -6px;
}
.todo-wrapper .todo-list .content .form .done {
  text-decoration: line-through;
  font-style: italic;
}
.todo-wrapper .todo-list .content .remove {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #ccc;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 10px;
  margin-top: -6px;
  color: #666;
}
.todo-wrapper .todo-list .content .remove:hover {
  color: #000;
}
.todo-wrapper .todo-list .edit input {
  padding: 4px;
  font-size: 16px;
}
.todo-wrapper .todo-state {
  height: 30px;
  line-height: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.todo-wrapper .todo-state span {
  font-size: 14px;
}
.todo-wrapper .todo-state .number {
  font-weight: bold;
  margin-right: 3px;
}
.todo-wrapper .todo-state a {
  margin-left: 10px;
  font-weight: bold;
}
.contact-list {
  min-width: fit-content;
  flex: 1;
  box-sizing: border-box;
  border: 1px solid #ccc;
  margin-right: 8px;
  margin-bottom: 8px;
}
.contact-list dt,
.contact-list dd {
  margin: 0;
}
.contact-list dt {
  padding: 10px;
  font-weight: bold;
  background-color: #eee;
  cursor: pointer;
  border-bottom: 1px solid #ddd;
}
.contact-list dt:hover {
  background-color: #ddd;
}
.contact-list dd {
  display: none;
  padding: 10px;
  border-bottom: 1px solid #bbb;
}
.contact-list .expanded dd {
  display: block;
}
.contact-list .expanded .checked {
  background: green;
}
.context-container {
  min-width: fit-content;
  flex: 1;
  box-sizing: border-box;
  background-color: #eee;
  margin-right: 8px;
  margin-bottom: 8px;
  overflow: auto;
}
.context-container .context-item {
  padding: 0 8px;
}
